<template>
  <div id="home">

    <nav-bar class="home-nav">
<!--      -->
      <div  slot="center"></div>
    </nav-bar>
    <tab-control :title="['','','']"
                 class="tabControl"
                 ref="tabControl1"
                 @tabClick="tabClick"
                 v-show="isShowTop"
    />
    <Scroll class="contents" ref="scroll"
            @scroll="scrollContent"
            :probeType="3"
            @pullUpLoad="loadMore"
            :pull-up-load="true"
    >
      <home-swiper :banners="banners" class="home-swiper"  @swiperImgLoad="swiperImgLoad"></home-swiper>
      <Recommendview :recommends="recommends"></Recommendview>
      <popularview></popularview>
      <tab-control :title="['','','']"
                   ref="tabControl2"
                   @tabClick="tabClick"
      />
      <goods-list :goods="goods[currentType].list"></goods-list>
      <!--                 @tabClick="tabClick"-->

    </Scroll>
      <back-top @click.native="backClick" v-show="isShowTback" />
  </div>
</template>

<script>
  //
  import NavBar from "components/common/navbar/NavBar"
  import Scroll from "components/common/scroll/Scroll"

  //
  import TabControl from "components/content/tabcontrol/TabControl"
  import GoodsList from "components/content/goods/GoodsList"
  import BackTop from "components/content/backTop/BackTop"
  //home
  import HomeSwiper from "./childcomps/HomeSwiper";
  import Recommendview from "./childcomps/Recommendview"
  import popularview from "./childcomps/popularview";

  import {getHomemultidata,getHomeGoods} from "network/home";
  import {debounce} from "common/untils"
  export default {
    name: "Home",
    data() {
      return {
        banners: [],
        recommends: [],
        goods: {
          'pop': {page: 0, list: []},
          'new': {page: 0, list: []},
          'sell': {page: 0, list: []}
        },
        currentType: 'pop',
        isShowTback: false,
        isShowTop: false,
        offsetTop: 0,
        TCindex: 0,
        itemImgListener: null

      }
    },
    components: {
      NavBar,
      Scroll,
      TabControl,
      GoodsList,
      BackTop,
      HomeSwiper,
      Recommendview,
      popularview,
      getHomemultidata,
      getHomeGoods,
    },
    created() {
      //methods
      this.getHomemultidata();
      //
      this.getHomeGoods('pop')
      this.getHomeGoods('new')
      this.getHomeGoods('sell')

    },
    mounted() {
      //goods
      // const  refresh = this.debounce(this.$refs.scroll.refresh,90)
      // this.$bus.$on('imgLoad',function(){
      // console.log('-------------------------')
      // refresh()
      // })
      // 
      const refresh = debounce(this.$refs.scroll.refresh,100);
      this.itemImgListener = ()=> {
        refresh();
      }
      this.$bus.$on("goodsImgLoad",this.itemImgListener)
    },
    deactivated(){
      // 
      this.$bus.$off('goodsImgLoad',this.itemImgListener)
    },
    methods:{
      // --------------------------------

        //tabContent
      swiperImgLoad(){
        // console.log('------------')
       this.offsetTop = this.$refs.tabControl2.$el.offsetTop
        // console.log(this.offsetTop)
      },
      tabClick(index){
        // console.log(index)
        this.TCindex = index
        switch (index) {
          case 0:
            this.currentType ='pop'
            break
          case 1:
            this.currentType ='new'
            break
          case 2:
            this.currentType ='sell'
            break
        }
        // this.$refs.constructor1.currentType=index  XXXXX  cunrrentIndextabControl.vuedata
        this.$refs.tabControl2.currentIndex = index;
        this.$refs.tabControl1.currentIndex = index;

      },
      backClick(){               //
        // refsrollbacktop
        this.$refs.scroll.scrollTo(0,0)
      },
      scrollContent(position){   //
        // console.log(position.y)  y
        // this.isShowTback = position.y> -1000  
        this.isShowTback = (-position.y) > 1000
        //tabcontrol
        this.isShowTop = this.offsetTop < (-position.y)
      },

      loadMore(){
        this.getHomeGoods(this.currentType)
        //bug
        this.$refs.scroll.refresh()
      },
      // -------------------------------
      //
      getHomemultidata(){
        getHomemultidata().then(res=>{
          //banner data
          this.banners = res.data.banner.list
          //recommendsdata
          this.recommends = res.data.recommend.list
        })
      },
      //
      // http://localhost:3000/home/data?type=new&page=1
      getHomeGoods(type){
        let page = this.goods[type].page + 1
        getHomeGoods(type,page).then(res=>{
          // this.goods = this.goods[type].list.push(...res.data.list)   
          this.goods[type].list.push(...res.data.list)
          this.goods[type].page += 1
          //
          this.$refs.scroll.finishPullUp()
        }).catch(err=>{
          console.log(err+'')
        })
      },

    }

  }
</script>

<style scoped>
  #home{
    /*height: 100%;*/
    /*padding-top: 44px;*/
    height: 100vh;
    position: relative;
  }
  .home-nav{
    background: var(--color-tint);
    color: #fff;
    /*better-scrollfixed*/

  }
  .home-swiper{
    /*padding-top: 44px;*/
  }
  .tabControl{
    /*tabControl,
    BScroll*/
    position: relative;
    top: -1px;
    left: 0;
    right: 0;
    z-index: 9;
  }
  .contents{
    overflow: hidden;
    /*height: 400px;*/
    /*height: calc(100% - 93px);*/
    position: absolute;
    top: 44px;
    left: 0;
    right: 0;
    bottom: 49px;
    /*width: 100%;*/
  }
</style>